import React, { useState } from "react";
import axios from "axios";

const UrlPredictor = () => {
  const [url, setUrl] = useState("");
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setResult(null);
    setError("");

    try {
      const response = await axios.post("http://34.64.139.6:8000/predict", {
        url: url,
      });
      setResult(response.data);
    } catch (err) {
      console.error(err);
      setError("서버 오류가 발생했습니다.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-white">
      <main className="flex-grow flex items-center justify-center">
      <form onSubmit={handleSubmit} className="space-y-4 w-full max-w-md mx-auto">
        <input
          type="text"
          value={url}
          onChange={(e) => setUrl(e.target.value)}
          placeholder="Enter URL"
          className="w-full p-3 border border-gray-300 rounded shadow"
          required
        />
        <button
          type="submit"
          className="w-full p-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition"
        >
          ✅ 검사하기
        </button>
      </form>
  
      <div className="mt-6">
        {loading && <p>🔄 분석 중...</p>}
        {error && <p className="text-red-500">❌ {error}</p>}
        {result && (
          <div className="mt-4 p-4 bg-gray-50 rounded shadow">
            <p className="mb-2">
              악성 확률:{" "}
              <strong>{(parseFloat(result.malicious_probability) * 100).toFixed(2)}%</strong>
            </p>
            <p>
              판별 결과:{" "}
              <strong className={result.is_malicious ? "text-red-600" : "text-green-600"}>
                {result.is_malicious ? "⚠️ 악성 URL" : "✅ 정상 URL"}
              </strong>
            </p>
          </div>
        )}
      </div>
      </main>
    </div>

  );
};

export default UrlPredictor;
